.display-screen {
  width: 100%;
  height: 70px;
  border: 1px solid #514e46;
  background: #c7c4ae;
  border-radius: 4px;
  box-shadow: inset 3px 5px 5px #797468, inset -1px -1px 5px #797468;
  text-align: right;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 30px;
  font-weight: 400;
  padding-right: 3px;
}

@media (max-width: 370px) {
  .display-screen {
    font-size: 25px;
  }
}
