.num-btn {
  box-shadow: 1px 1px 1px #000, -3px -3px 3px #fff;
  border-radius: 4px;
  border: none;
  background: #f0f0f0;
  cursor: pointer;
  font-size: 1.3rem;
}

.num-btn:hover {
  background: #60645b;
  color: #f0f0f0;
}

.num-btn:active {
  background: #797468;
  box-shadow: inset 1px 1px 1px #000, inset -2px -2px 3px #fff !important;
}

.num-btn:nth-child(1) {
  grid-column: 1 / 3;
  background: #60645b;
  box-shadow: 1px 2px 1px #000;
  color: #f0f0f0;
}

.num-btn:nth-child(1):hover {
  grid-column: 1 / 3;
  background: #242522;
}

.num-btn:nth-child(15) {
  grid-row: 4 / 6;
  grid-column: 4 / 5;
  background: #f2521d;
  box-shadow: 1px 2px 1px #000;
}

.num-btn:nth-child(15):hover {
  background: #e24611;
  box-shadow: 1px 1px 1px #000;
}

.num-btn:nth-child(16) {
  grid-column: 1 / 3;
}

@media (max-width: 350px) {
  .num-btn {
    font-size: 1rem;
  }
}
