*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --ff-general: "Russo One", sans-serif;
}

html {
  user-select: none;
}

body {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
  background: #eaeff0;
  padding-inline: 15px;
  position: relative;
}

body * {
  font-family: var(--ff-general);
}

.App {
  width: 350px;
  margin: 0 auto;
  padding: 30px;
  border-radius: 7px;
  box-shadow: 0 0 20px #8e8a88;
  background: #e7e1dc;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

@media (max-width: 370px) {
  body {
    padding: 0;
  }
  .App {
    width: 250px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
